.thought-container{
    display: flex;
    background-color: var(--selected-inquiry-background-color);
    padding: 1rem;
    margin-bottom: 2rem;
    position: sticky;
    top: 0;
    box-shadow: 0 8px 6px -6px var(--selected-inquiry-background-color);
    z-index: 2;
}

.questions-container{
    margin: auto;
    /* max-width: 600px; */
    width: clamp(50%, 600px, 100%);
}

.question-container{

    background-color: var(--question-color);
    border: 2px solid var(--selected-inquiry-background-color);
    border-radius: 1rem;
    padding: 1rem;
    margin: 1.5rem 1rem 1rem 1rem;

    box-shadow: 0 8px 6px -6px var(--selected-inquiry-background-color);
}

.overlay{
    position: absolute;
    right: auto;
    left: auto;
    width: 100%;
    animation: fadeInAnimation .3s;
    background-color: rgba(0, 0, 0, 0.37);

    display: flex;
    justify-content: center;
}
.inquiry-view-container{

    outline: 1px solid black;
    background-color: var(--background-color);
    width: min(100%, 600px);
    max-height: 100%;
    overflow-y: scroll;

    position: fixed;
    top: 0px;
    right: auto;
    left: auto;
    width: 100%;
    animation: fadeInAnimation .3s;
}


@keyframes fadeInAnimation {
  from {
      opacity: 0;
      transform: scale(0.9);
    }
  to {
      opacity: 1;
      transform: scale(1);
    }
}
.close-animation{
    animation: closeAnimation .3s;
}

@keyframes closeAnimation {
    from{
        opacity: 1;
        transform: scale(1);
    }
    to{
        opacity:0;
        transform: scale(.9);
    }
}


.answer{
    font-size: 1rem;
    margin-top: 1rem;
}


.input-field{
    width: 100%;
    line-height: 1.4em;
    resize: none;
    border: none;
    outline: none;
    color: var(--text-color);

    /* ************************* */
    overflow: hidden;
}

.question{
    font-size: 1.2rem;
    color: var(--selected-inquiry-background-color);
    font-weight: bold;
}
.thought-title{
    font-size: 1.4rem;
    color: white;
    background-color: var(--selected-inquiry-background-color);
    resize: none;
    border: none;
    width: 100%;
    font-weight: bold;
}